
import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path:'/',
    redirect: '/Home'
  },
  {
    path: '/Home',
    component: Home
  },
  {
    path: '/Boostproject',
    component: () => import('@/views/Boostproject/Boostproject.vue'),
    redirect: '/Boostproject/mfBoots',
    children:[
      {
        path: '/Boostproject/mfBoots',
        component: () => import( '@/views/Boostproject/mfBoots.vue'),
      },
      {
        path: '/Boostproject/afBoots',
        component: () => import( '@/views/Boostproject/afBoots.vue'),
      },
      {
        path: '/Boostproject/ofBoots',
        component: () => import( '@/views/Boostproject/ofBoots.vue'),
      },
      {
        path: '/Boostproject/ydBoots',
        component: () => import( '@/views/Boostproject/ydBoots.vue'),
      },
      {
        path: '/Boostproject/mzBoots',
        component: () => import('@/views/Boostproject/mzBoots.vue'),
      },
      {
        path: '/Boostproject/dsBoots',
        component: () => import('@/views/Boostproject/dspw.vue'),
      },
    ]
  },
  {
    path: '/Agreement',
    component: () => import('@/views/Agreement.vue'),

  },
  {
    path: '/contact',
    component: () => import('@/views/Contact.vue'),

  },
]

const router = new VueRouter({
  routes
})

export default router
